import React from "react";
import s from './Navbar.module.css'
import {NavLink} from "react-router-dom";
import {S} from '../styles/_navbarSstyles'

export const Navbar = () => {
    return (
        <nav className={s.nav}>
            <S.NavWrapper><NavLink to={'/profile'}></NavLink></S.NavWrapper>
            <S.NavWrapper><NavLink to='/dialogs'></NavLink></S.NavWrapper>
            <S.NavWrapper><NavLink to="/news"></NavLink></S.NavWrapper>
            <S.NavWrapper><NavLink to="/music"></NavLink></S.NavWrapper>
            <S.NavWrapper><NavLink to="/setting"></NavLink></S.NavWrapper>
            <S.NavWrapper><NavLink to="/users"></NavLink></S.NavWrapper>
        </nav>
    )
}